import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsAccountsListComponent } from 'src/app/commonBussinessSRC/psComponents/ps-accounts-list/ps-accounts-list.component';
import { IOptionsPSComplexDateFilter } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-date-filter/ps-complex-date-filter.component.interfaces';
import { PsComplexExchangeComponent } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-exchange/ps-complex-exchange.component';
import { IOptionsPsComplexExchnageExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-exchange/ps-complex-exchange.component.interfaces';
import { IOptionsPSComplexRecurringSchedulerExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-recurring-scheduler/ps-complex-recurring-scheduler.component.interfaces';
import { IOptionsPsLookupOwnAccountsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-accounts/ps-lookup-own-accounts.component.interfaces';
import { IOptionsPsLovInstructionMethodExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-lov/ps-lov-instruction-method/ps-lov-instruction-method.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { IValidateCurrencyCombination } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';


@Component({
  selector: 'app-sweeping-and-pooling',
  templateUrl: './sweeping-and-pooling.page.html',
  styleUrls: ['./sweeping-and-pooling.page.scss'],
})
export class SweepingAndPoolingPage extends OmniBasePage implements OnInit {
  public formGroup: FormGroup = new FormGroup({});
  public sweepingAndPoolingVO: any = {};
  public exchangeRate = '0.00';
  public amountInToAccountAmount = '0.00';
  @ViewChild(PsComplexExchangeComponent) exchangeChild: PsComplexExchangeComponent;
  @ViewChild('toAccountRef') toAccountRef;
  @ViewChild('fromAccountRef') fromAccountRef;
  @ViewChild('targetAccountRef') targetAccountRef;
  @ViewChild('exchangeRateRef') exchangeRateRef;

  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'sweeping_and_pooling_key',
    isHorizontalStepper: true,
    numberOfSteps: 2,
    namesofSteps: [
      'sweeping_and_pooling_step1_key',
      'periodicity_step2_key',
    ],
    group: this.formGroup,
    submitOptions: {
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.sweepingAndPoolingRequestEndPoint,
      group: this.formGroup,
    },
    requestObject: this.sweepingAndPoolingVO,
  };
  public panelSweepingOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'sweeping_and_pooling_main_details_key',
    expanded: true
  };
  public panelPeriodicityOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'periodicity_details_key',
    expanded: true
  };
  instructionMethodOptions: IOptionsPsLovInstructionMethodExposed = {
    group: this.formGroup,
    fcName: 'instructionMethod',
  };
  instructionTypeOptions: IOptionsPsLovInstructionMethodExposed = {
    group: this.formGroup,
    fcName: 'instructionType',
  };
  public targetAccountListOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'target_account_key',
    placeHolder: 'target_account_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'fromAccount',
    accountType: 'G',
    fromTo: 'from',
    requestObject: this.sweepingAndPoolingVO,
  };
  public coveringAccountListOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'covering_account_key',
    placeHolder: 'covering_account_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'toAccount',
    accountType: 'G',
    fromTo: 'from',
    requestObject: this.sweepingAndPoolingVO,
  };

  public receivingAccountListOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'receiving_account_key',
    placeHolder: 'receiving_account_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'toAccount',
    accountType: 'G',
    fromTo: 'to',
    requestObject: this.sweepingAndPoolingVO,
  };

  public schedulerOptions: IOptionsPSComplexRecurringSchedulerExposed = {
    fcName: 'psScheduler',
    group: this.formGroup,
    periodaicityInputCountOptions: {
      fcName: 'noOfPayments',
    },
    requestObject: this.sweepingAndPoolingVO,
  };
  public complexExchangeOptions: IOptionsPsComplexExchnageExposed = {
    editableMode: false,
    showToCurrencyOptions: true,
    fromAmountOptions: {
      currency: '',
      currenciesOptions: {
        placeHolder: 'currency_key',
        labelKey: 'currency_key',
        fcName: 'fromCurrency',
        group: this.formGroup
      },
      amountOptions: {
        labelKey: 'amount_key',
        placeHolder: 'enter_amount_key',
        fcName: 'transactionAmount',
        group: this.formGroup,
        decimalPoints: 2
      }
    },
    toAmountOptions: {
      currency: '',
      currenciesOptions: {
        placeHolder: 'currency_key',
        labelKey: 'currency_key',
        fcName: 'currency',
        group: this.formGroup
      },
      amountOptions: {
        labelKey: 'exchange_amount_key',
        placeHolder: 'enter_amount_key',
        fcName: 'toAmount',
        group: this.formGroup,
        decimalPoints: 2
      }
    },
    requestObject: this.sweepingAndPoolingVO,
    group: this.formGroup
  };

  instructionType: any;
  showIsMinimumOrMax = false;
  hideDates = true;

  defaultFormat: any = 'MM/DD/YYYY';
  filterDate: IOptionsPSComplexDateFilter = {
    fromDate: {
      group: this.formGroup,
      fcName: 'filterFromDate',
      displayFormat: this.defaultFormat,
      placeHolder: 'from_date_key',
    },
    toDate: {
      group: this.formGroup,
      fcName: 'filterToDate',
      displayFormat: this.defaultFormat,
      placeHolder: 'to_date_key',
    }
  };

  public toAccountComponent: any = {};
  showDate = false;
  showMinimumtoAccount = false;
  showReceivingAccount = false;
  showCoveringAccount = false;
  showsheduler: boolean = false;

  constructor(
    public commonService: PsCommonService,
    public logger: LoggerService,
    public omniPull: OmniPullService,
    private navService: PsNavigatorService) {
    super();
  }

  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    const navParams = this.navService.getAllParams();
    this.commonProv.copyObject(this.sweepingAndPoolingVO, navParams?.ScreenVO ? navParams.ScreenVO : navParams);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.stepperOptions.namesofSteps[1]], 0, true);
  }

  ngAfterViewInit(): void {
    if (this.stepperOptions.requestObject && this.stepperOptions.requestObject.isEditScreen === true) {
      this.commonProv.setValInsideNestedObj(this.instructionMethodOptions.fcName, this.sweepingAndPoolingVO.instructionMethodCode, this.stepperOptions.requestObject);
      this.commonProv.setValInsideNestedObj(this.instructionTypeOptions.fcName, this.sweepingAndPoolingVO.instructionTypeCode, this.stepperOptions.requestObject);
      if (this.sweepingAndPoolingVO.instructionTypeCode === CommonBussinessConstant.INSTRUCTION_TYPE_MIN || this.sweepingAndPoolingVO.instructionTypeCode === CommonBussinessConstant.INSTRUCTION_TYPE_AUTO) {
        this.commonProv.setValInsideNestedObj(this.coveringAccountListOptions.fcName, this.sweepingAndPoolingVO?.toAccount, this.stepperOptions.requestObject);
      } else {
        this.commonProv.setValInsideNestedObj(this.receivingAccountListOptions.fcName, this.sweepingAndPoolingVO?.toAccount, this.stepperOptions.requestObject);
      }
      this.commonProv.setValInsideNestedObj('toAccount_lookupKey', this.sweepingAndPoolingVO.toAccount, this.stepperOptions.requestObject);
      this.commonProv.setValInsideNestedObj(this.targetAccountListOptions.fcName, this.sweepingAndPoolingVO?.targetAccAdditonalRef, this.stepperOptions.requestObject);
      this.commonProv.setValInsideNestedObj('fromAccount_lookupKey', this.sweepingAndPoolingVO.targetAccAdditonalRef, this.stepperOptions.requestObject);

    }
  }

  public onFromAccountChange(fromAccount) {
    this.complexExchangeOptions.fromAmountOptions.currencyCode = fromAccount.currency;
    setTimeout(() => {
      this.stepperOptions.requestObject[this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName] = fromAccount.currency;
    }, 1);

    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.toAmountOptions.amountOptions.fcName], 1);

    if (this.showReceivingAccount) {
      this.receivingAccountListOptions.accountNumber = fromAccount.accountNumber;
      this.toAccountRef.refreshAcounts(this.receivingAccountListOptions);
    } else if (this.showCoveringAccount) {
      this.coveringAccountListOptions.accountNumber = fromAccount.accountNumber;
      this.fromAccountRef.refreshAcounts(this.coveringAccountListOptions);
    }
    this.callCurrencyCombinationValidateService(fromAccount);
  }

  public onToAccountChange(toAccount) {
    this.complexExchangeOptions.toAmountOptions.currencyCode = toAccount.currency;
    setTimeout(() => {
      this.stepperOptions.requestObject[this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName] = toAccount.currency;
    }, 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.toAmountOptions.amountOptions.fcName], 1);

    if (this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName] == null ||
      this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName] == undefined)
      this.targetAccountListOptions.accountNumber = toAccount.accountNumber;
    this.targetAccountRef.refreshAcounts(this.targetAccountListOptions);
    this.callCurrencyCombinationValidateService(toAccount);

  }

  onChangeSchedule(value) {
    let stateDay;
    if (this.navService.getAllParams().ScreenVO === undefined) {
      if (value !== undefined) {
        if (value.priodicityBy === CommonBussinessConstant.SINGLE_FUTURE_DATE || value.priodicityBy === CommonBussinessConstant.RECURRING_MONTHLY) {
          stateDay = value.startDate.getDay() + 1;
          if (value.priodicityBy === CommonBussinessConstant.SINGLE_FUTURE_DATE) {
            this.sweepingAndPoolingVO['submitType'] = '4';
          } else {
            stateDay = value.startDate.getDate();
            this.sweepingAndPoolingVO['submitType'] = '5';
          }
        }
        if (value.priodicityBy === CommonBussinessConstant.RECURRING_WEEKLY) {
          stateDay = value.startDate.getDay() + 1;
          this.sweepingAndPoolingVO['submitType'] = '5';
        }
      }
      this.sweepingAndPoolingVO['day'] = stateDay
    }
  }

  onInstructionChange(event) {
    if (event != null && event !== undefined) {
      if (this.sweepingAndPoolingVO[this.coveringAccountListOptions.fcName] || this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName]) {
        this.targetAccountRef?.reset();
        this.toAccountRef?.reset();
        this.fromAccountRef?.reset();
        this.exchangeRateRef?.reset();
        this.targetAccountListOptions.accountNumber = null;
        this.coveringAccountListOptions.accountNumber = null;
        this.receivingAccountListOptions.accountNumber = null;
      }

      if (event.itemValue === CommonBussinessConstant.INSTRUCTION_TYPE_MIN || event.itemValue === CommonBussinessConstant.INSTRUCTION_TYPE_AUTO) {
        this.commonProv.copyObject(this.targetAccountListOptions, {
          fromTo: 'to',
        }, false);
        if (this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName] !== undefined && this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName] != null) {
          this.coveringAccountListOptions.accountNumber = this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName].accountNumber;
        }
        this.showCoveringAccount = true;
        this.showReceivingAccount = false;
        this.showIsMinimumOrMax = event.itemValue === CommonBussinessConstant.INSTRUCTION_TYPE_MIN;
      } else {
        this.commonProv.copyObject(this.targetAccountListOptions, {
          fromTo: 'from',
        }, false);
        if (this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName] !== undefined
          && this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName] != null) {
          this.receivingAccountListOptions.accountNumber = this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName].accountNumber;
        }
        this.showReceivingAccount = true;
        this.showCoveringAccount = false;
        this.showIsMinimumOrMax = true;
      }
    }
  }


  onInstructionMethodChange(event) {
    if (event.itemValue === CommonBussinessConstant.INSTRUCTION_METHOD_OFFLINE_CODE) {
      this.stepperOptions.numberOfSteps = 2;
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.stepperOptions.namesofSteps[1]], 1, true);
      this.showDate = false;
      this.showsheduler = true;

      this.schedulerOptions.requestObject = this.sweepingAndPoolingVO;
    } else {
      this.showDate = true;
      this.stepperOptions.numberOfSteps = 1;
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.stepperOptions.namesofSteps[1]], 0, true);
      this.showsheduler = false;
    }

  }

  //added by Marina-Jira-AZDB-252; to check currency combination validation
  callCurrencyCombinationValidateService(account) {
    if (account[ConstantCommon.VALIDATE_COMBINATION]) {
      //check if both from account and to account are defined to avoid unnecessary service calls
      if (this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName]
        && (this.sweepingAndPoolingVO[this.receivingAccountListOptions.fcName] || this.sweepingAndPoolingVO[this.coveringAccountListOptions.fcName])) {
        let validationRequest: IValidateCurrencyCombination = {
          fromCurrency: this.sweepingAndPoolingVO[this.targetAccountListOptions.fcName].currency,
          listOfAffectedFields: [this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName, this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName],
          screenVO: this.sweepingAndPoolingVO,
          listOfAccounts: [this.targetAccountRef]
        };
        //in case of receiving account, add receiving account data to request
        if (this.showReceivingAccount) {
          validationRequest.toCurrency = this.sweepingAndPoolingVO[this.receivingAccountListOptions.fcName].currency;
          validationRequest.listOfAffectedFields.push(this.receivingAccountListOptions.fcName);
          validationRequest.listOfAccounts.push(this.toAccountRef);
        }
        //else, add covering account data to request
        else {
          validationRequest.toCurrency = this.sweepingAndPoolingVO[this.coveringAccountListOptions.fcName].currency;
          validationRequest.listOfAffectedFields.push(this.coveringAccountListOptions.fcName);
          validationRequest.listOfAccounts.push(this.fromAccountRef);
        }
        //check that both from and to currencies are defined before calling validation service
        if (validationRequest.fromCurrency && validationRequest.toCurrency) {
          if (this.exchangeRateRef) {
            validationRequest.listOfAccounts.push(this.exchangeRateRef);
          }
          this.omniPull.validateCurrencyCombination(validationRequest);
        }
      }
      //Added by ghada, AZDB-1666 for fixing the issue of having from date and to date controls being invalid on submit although they 're not exist on the screen
      const fromDateCust = this.commonProv.getElementValidations(this.filterDate.fromDate.fcName)
      const toDateCust = this.commonProv.getElementValidations(this.filterDate.toDate.fcName)
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.filterDate.fromDate.fcName], this.showDate && fromDateCust.IS_MANDATORY)
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.filterDate.toDate.fcName], this.showDate && toDateCust.IS_MANDATORY)

    }
  }
}